import "bootstrap/dist/css/bootstrap.min.css";
import "./CounterStyle.css";
import CounterItem from "./CounterItem";
function Counter() {
  return (
    <section className="counter">
      <div className="container">
        <div className="row">
          <CounterItem num="12" text="Years Of Experiance" />
          <CounterItem num="85" text="Success Project" />
          <CounterItem num="15" text="Active Project" />
          <CounterItem num="95" text="Happy CUstomers" />
        </div>
      </div>
    </section>
  );
}

export default Counter;
