import "./NavBarStyle.css";
import Logo from "../assets/images/Logo.png";
import Vector from "../assets/images/Vector.png";
function NavBar() {
  const NavBarList = [
    "Home",
    "Pages",
    "Services",
    "Project",
    "Blog",
    "Contact",
  ];
  return (
    <nav className="navbar navbar-expand-lg bg-body py-4">
      <div className="container">
        <img src={Logo} alt="h" />
        <a className="navbar-brand ms-lg-2" href="#">
          Interno
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 text-dark">
            {NavBarList.map((name) => (
              <li className="nav-item" key={name}>
                {name}
              </li>
            ))}
            <li className="nav-item">
              <img src={Vector} className="d-none d-md-block" alt="dsa" />
              <form className="d-flex d-md-none" role="search">
                <input
                  className="form-control d-inline"
                  type="search"
                  placeholder="Search"
                  aria-label="Search"
                />
              </form>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
